.ContactForm {
  outline: 1px solid black;
  padding: 15px;
}

.InputOverlay {
  margin-bottom: 20px;
}

.InputLabel {
  display: block;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 500;
}

.Input {
  display: block;
  border: 2px solid rgb(206, 205, 205);
}

.FormButton {
  font: inherit;
  font-size: 14px;
  cursor: pointer;
  outline: none;
  padding: 2px 5px 2px 5px;
  border: 2px solid rgb(206, 205, 205);
  border-radius: 3px;
}
